body {
    font-family: sans-serif;
}

* {
    box-sizing: border-box;
}

h1 {
    font-weight: 100;
    margin-bottom: 0;
    color: purple;
}

.main-header {
    /* margin: 0 20px; */
    max-width: 70vw;
    margin-left: auto;
    margin-right: auto;
    padding: 0 10px;
    display: flex;
}

.main-header .toolbar {
    text-align: right;
    flex-grow: 1;
    margin-top: 25px;
}

.main-header .toolbar .button {
    font-size: 1.5em;
    color: grey;
    cursor: pointer;
    transition: color 300ms ease-in;
}

.main-header .toolbar .button:hover {
    color: black;
}

.slice {
    border: 1px solid #d8d8d8;
    border-radius: 2px;
    min-height: 100px;
    box-shadow: 1px 2px 7px rgba(0, 0, 0, 0.17);
    transition: opacity 200ms ease-in;
    margin-right: auto;
    margin-left: auto;
    min-width: 200px;
    height: 100%;

    display: flex;
    flex-direction: column;
}

.slice.dragging {
    opacity: 0.3;
}

.slice .body {
    padding: 10px;
    overflow: hidden;
    flex-grow: 1;
}

.slice .header {
    padding: 5px 10px;
    /* color: white; */
    background: #efefef;
    border-bottom: 1px solid #d8d8d8;
    cursor: pointer;

    display: flex;
}

.slice .header .button {
    border-radius: 3px;
    font-size: 17px;
    margin: 0 3px;
    display: inline-block;
    position: relative;
    cursor: pointer;
    right: -6px;
    padding: 2px 5px;
    color: gray;
}

.slice .header .button-toggle {
    font-size: 11px;
    margin-top: 5px;
    display: inline-block;
    position: relative;
    cursor: pointer;
    color: gray;
    margin-right: 5px;
    -webkit-user-select: none;
}

.slice .header .button-toggle.active {
    color: purple;
}

.slice .header .button:hover {
    color: black;
}

.slice .header .button.active {
    color: purple;
}

.slice .header .title {
    font-size: medium;
    width: 100%;
    border: none;
    background: transparent;
    outline: none;
}

.slice .header ::-webkit-input-placeholder {
    font-style: italic;
}

.slice .footer {
    padding: 5px 10px;
    background: #f5f5f5;
    border-bottom: 1px solid #d8d8d8;
}

.time-slider {
    width: 100%;
    position: relative;
    left: -2px;
}

.widget {
    flex-grow: 1;
}

.slice.reference {
    box-shadow: 1px 2px 7px rgba(94, 8, 148, 0.27);
    border: 1px solid purple;
}

.slice.reference .header {
    background: #f2eaff;
}
