svg.measure {
    position: absolute;
    left: -1000px;
    top: -1000px;
}

circle.active {
    fill: white;
}

rect.inactive {
    fill: #bbb;
}

rect.active {
    fill: red;
}

.genesis {
    fill: black;
}

text {
    alignment-baseline: central;
    fill: white;
    font-size: small;
    pointer-events: none;
    -webkit-user-select: none;
}

line.mainline {
    stroke: black;
    stroke-width: 2;
}

line.inactive {
    stroke: #bbb;
    stroke-width: 1;
}

path.mainline {
    stroke: black;
    stroke-width: 2;
    fill: none;
}

path.inactive {
    stroke: #bbb;
    stroke-width: 1;
    fill: none;
}

svg.dag {
    width: 100%;
}

.row-1 {
    max-width: 70vw;
    margin-left: auto;
    margin-right: auto;
}

.row > * {
    display: flex;
}
.row {
    border-bottom: 5px solid transparent;
}
.row:first-child {
    border-top: 5px solid transparent;
}

.row.insert-top {
    border-top: 5px solid purple;
}

.row.insert-bottom {
    border-bottom: 5px solid purple;
}

.col {
    flex-basis: 0;
    flex-grow: 1;
    border-right: 5px solid transparent;
    padding: 5px;
    overflow: hidden;
}
.col:first-child {
    border-left: 5px solid transparent;
}

.col.insert-left {
    border-left: 5px solid purple;
}

.col.insert-right {
    border-right: 5px solid purple;
}

.fake-slice {
    border: 2px dashed #d8d8d8;
    border-radius: 2px;
    min-height: 30px;
    padding-bottom: 20px;
    text-align: center;
    font-size: 100px;
    color: #d8d8d8;
    -webkit-user-select: none;
    cursor: pointer;

    transition: all 200ms ease-in;
}

.fake-slice:hover {
    border: 2px dashed gray;
    color: gray;
}

.example-enter {
    /* 0 does not work so we have to use a small number */
    /* Start our small */
    flex: 0.00001 1;

    -webkit-animation: flexGrow 300ms ease forwards;
    animation: flexGrow 300ms ease forwards;
}

.example-leave {
    flex: 1 1;

    -webkit-animation: flexShrink 300ms ease forwards;
    animation: flexShrink 300ms ease forwards;
}

@-webkit-keyframes flexGrow {
    to {
        flex: 1 1;
    }
}
@keyframes flexGrow {
    to {
        flex: 1 1;
    }
}

@-webkit-keyframes flexShrink {
    to {
        flex: 0.01 1;
        flex: 0.00001 1;
    }
}
@keyframes flexShrink {
    to {
        flex: 0.01 1;
        flex: 0.00001 1;
    }
}

.CodeMirror {
    height: auto !important;
    min-height: 100px;
    font-size: 13px;
}

.editor .CodeMirror pre {
    padding: 0 10px;
}

.editor .CodeMirror-lines {
    padding: 10px 0;
}

.deleted {
    color: #b30000;
    background: #fadad7;
    cursor: pointer;
}

.inserted {
    background: #eaf2c2;
    color: #406619;
    cursor: pointer;
}

.line-inserted {
    background: #c9f1c9;
}

.line-changed {
    background: #ffffbb;
}

.line-removed {
    background: #ffd7d7;
    padding: 0 10px;
}

span.result {
    background: #dedede;
    padding: 0 4px;
    margin: 0 5px;
    border-radius: 2px;
}

